import React from 'react';
import { withNamespaces } from 'react-i18next';
import { withSitecoreContext, Text } from '@sitecore-jss/sitecore-jss-react';
import clsx from 'clsx';
import Hidden from '@material-ui/core/Hidden';
import get from 'lodash/get';
import useStyles from './style';
import Typography from '../../../components/atoms/Typography';
import CardMedia from '../../../components/atoms/CardMedia';
import Card from '../../../components/molecules/Card';
import CardContent from '../../../components/atoms/CardContent';
import Tabs from '../../../components/molecules/Tabs';
import Title from '../../../components/atoms/Title';
import Button from '../../../components/atoms/Button';
import withErrorBoundary from '../../../utils/HOCs/withErrorBoundary';
import { pushAnaylyticsData } from '../../../../../src/utils/analytics';
import { analyticsData } from '../../../../../src/constants/Analytics/analytics-data';
import { COMPONENT_NAME } from './locators';

const TabsWithArticle = ({ fields }) => {
  const classes = useStyles();
  const { title, segmentText, articleTabs } = fields;

  const addAnalyticsInfo = (titleText, linkField) => {
    const obj = {
      ...get(analyticsData, 'pageBodyInteraction.arrowClick'),
      ...{ label: `${titleText && titleText.value} - ${get(linkField, 'value.text')}` },
    };
    pushAnaylyticsData(obj);
  };
  const addTileAnalyticsInfo = imgTitle => {
    const obj = {
      ...get(analyticsData, 'pageBodyInteraction.tileClick'),
      ...{ label: `${title && title.value}-${imgTitle && imgTitle.value}` },
    };
    pushAnaylyticsData(obj);
  };

  const tabData = ({ image, title, link, backgroundImage }) => (
    <div className="tabContentContainer">
      <Card
        onClick={() => addTileAnalyticsInfo(title)}
        className={clsx(classes.root, 'cardWrapper')}
        square
        elevation={0}
      >
        <CardMedia className="card-media" image={image} alternateAltText={title && title.value} />
        <CardContent className={clsx(classes.content)}>
          <Typography
            gutterBottom
            variant="h5"
            component="h2"
            className="card-title"
          >
            <Text field={title} />
          </Typography>
          <Button
            onClick={() => addAnalyticsInfo(title, link)}
            component="span"
            buttonType="primary"
            field={link}
          />
        </CardContent>
      </Card>
      <div className="map-image">
        <CardMedia
          className="card-media-map"
          image={backgroundImage}
          scaleOnHover={false}
          alternateAltText={title && title.value}
        />
      </div>
    </div>
  );

  const createTabs = articleTabs && articleTabs.map(articalTab => {
    const { tabText } = articalTab.fields;

    return {
      data: tabData(articalTab.fields || {}),
      label: tabText && tabText.value,
    };
  });

  const renderTitle = type => (
    <Typography
      variant={type}
      component={type}
      gutterBottom
      className={classes.heading}
    >
      <Text field={title} />
    </Typography>
  );

  return (
    <div className={classes.tabsWithArticle} data-locator={COMPONENT_NAME}>
      <Title text={segmentText} />
      {title && (<Hidden mdUp implementation="css">{renderTitle('h5')}</Hidden>)}
      {title && (<Hidden smDown implementation="css">{renderTitle('h2')}</Hidden>)}
      <Tabs tabs={createTabs} className="tabs-container" />
    </div>
  );
};

export default withErrorBoundary(
  withSitecoreContext()(withNamespaces()(TabsWithArticle))
);
