import { makeStyles } from '@material-ui/core/styles';
import { pxToRem } from '../../../../styles/themes/vad/constants';

const useStyles = makeStyles(theme => {
  const { right } = theme.mixins;

  return {
    tabsWithArticle: {
      ...theme.mixins.paddingLR(pxToRem(24), pxToRem(24)),

      [theme.breakpoints.up('md')]: {
        padding: `${pxToRem(88)} 0`,
        ...theme.mixins.paddingLR(pxToRem(81), pxToRem(91)),
        ...theme.shape.componentSpacing,
      },

      '& .tabContentContainer': {
        display: 'flex',
        flexDirection: 'column-reverse',
        position: 'relative',

        [theme.breakpoints.up('md')]: {
          flexDirection: 'row',
        },
      },

      '& .map-image': {
        padding: `0 ${pxToRem(30)}`,

        [theme.breakpoints.up('md')]: {
          position: 'absolute',
          width: '52%',
          ...right(0),
          top: pxToRem(-50),
          padding: 0,
        },
      },

      '& .cardWrapper': {
        background: 'transparent',
        position: 'relative',
        zIndex: 1,
        marginTop: '-13px',

        [theme.breakpoints.up('md')]: {
          width: '50%',
          marginTop: 0,
        },
      },
    },
  };
});

export default useStyles;
